section#resume {
  min-height: 77.2vh;
  padding-top: 15px;
}
section#resume h1 {
  color: white;
  font-size: 2em;
}
section#resume .tabs-icon {
  margin: 0 auto 30px auto;
  float: none;
  color: #fff;
}
section#resume .tabs-icon li {
  display: inline-block;
  padding: 10px 2vh;
  background: #154f81;
  border: 0.5px #272f35 solid;
  color: #fff;
  border-radius: 30px;
  /*margin: 0 -2px;*/
  cursor: pointer;
}
section#resume .tabs-icon li:first-child {
  border-bottom-right-radius: 0;
  border-top-right-radius: 0;
}
section#resume .tabs-icon li:last-child {
  border-bottom-left-radius: 0;
  border-top-left-radius: 0;
}
section#resume .tabs-icon li.active {
  -webkit-box-shadow: inset 0px 0px 10px #272f35;
          box-shadow: inset 0px 0px 10px #272f35;
}
section#resume .tabs-icon li h6 {
  text-transform: uppercase;
  font-size: 13px;
  font-weight: 700;
  letter-spacing: 1px;
  display: inline-block;
  padding-left: 10px;
  margin: 0;
}
section#resume .resume-content h6 {
  margin: 0;
  color: #eee;
}
section#resume .resume-content P {
  color: #eee;
}
section#resume .resume-content [class*="col-"] {
  padding-top: 0;
  padding-bottom: 0;
}
section#resume .resume-content .main-icon {
  width: 50px;
  height: 50px;
  line-height: 50px;
  border-radius: 50%;
  border: 2px solid #eee;
  color: #eee;
  font-size: 25px;
  margin: 0 auto 50px;
  text-align: center;
  position: relative;
}
section#resume .resume-content .main-icon:after {
  content: '';
  width: 2px;
  height: 45px;
  background: #eee;
  position: absolute;
  bottom: -45px;
  left: calc(50% - 1px);
}
section#resume .resume-content .inf .date {
  font-size: 11px;
  font-weight: 600;
  padding: 7px 18px;
  background: #eee;
  color: #154f81;
  border-radius: 20px;
  position: relative;
}
section#resume .resume-content .cont {
  position: relative;
  /*Move border side line to right*/
  /* Experience fix*/
  /*Move bullets*/
  /* border side line */
  /* hide last one */
  /*Bullets*/
}
section#resume .resume-content .cont.text-right:before {
  left: calc(100% + 16px);
}
section#resume .resume-content .cont.text-right.pb-50.no-border:before {
  display: none;
}
section#resume .resume-content .cont.text-right:after {
  left: calc(100% + 8px);
}
section#resume .resume-content .cont:before {
  content: '';
  width: 2px;
  height: calc(100% - 30px);
  background: #eee;
  position: absolute;
  top: 22px;
  left: -15px;
}
section#resume .resume-content .cont.last:before {
  display: none;
}
section#resume .resume-content .cont:after {
  content: '';
  width: 15px;
  height: 15px;
  border-radius: 50%;
  border: 1px solid #154f81;
  background: #154f81;
  position: absolute;
  top: 0px;
  left: -23px;
  z-index: 9;
}



